.inventory-standing-book {
  width: 98%;
  height: 98%;
  margin-left: 10px;
  margin-top: 10px;
  background: white;
  padding: 0 12px;
  box-sizing: border-box;
  overflow-y: scroll;

  &-header {
    width: 100%;
    height: 80px;
    display: flex;
    align-items: center;
    position: relative;
    justify-content: flex-end;
  }

  &-right {
    width: 400px;
    height: 100%;
    //position: absolute;
    //right: 0px;
    display: flex;
    justify-content: flex-end;
    align-items: center;

  }

  &-table {
    width: 100%;
    //min-height: 300px;
  }

  &-detail {
    font-size: 14px;
    font-family: SourceHanSansSC-Normal, SourceHanSansSC;
    font-weight: 400;
    color: #096DD9;
    line-height: 20px;
    text-shadow: -11px 2px 22px rgba(0, 0, 0, 0.16);
    cursor: pointer;
  }

  .ant-btn[disabled],
  .ant-btn[disabled]:hover {
    background: white;
  }

}
@primary-color: #1890ff;